import {CURRENT_DATE} from '../const.js';

// -   -
const filtersList = [
  {
    name: 'everything',
    condition: () => true
  },
  {
    name: 'future',
    condition: (event) => new Date(event.dateFrom) > CURRENT_DATE //  event.dateFrom   , return true;
  },
  {
    name: 'present',
    condition: (event) => (new Date(event.dateFrom) < CURRENT_DATE) && (new Date(event.dateTo) > CURRENT_DATE) //  event.dateFrom   ,  event.dateTo - , return true;
  },
  {
    name: 'past',
    condition: (event) => new Date(event.dateTo) < CURRENT_DATE //  event.dateTo   , return true;
  },
];

function generateFilters(events) { //         
  return filtersList.map((filter) => ({name: filter.name, enabled: events.some((event) => filter.condition(event))})); //,       
  // const filters = [];
  // filtersList.forEach((filter) => {
  //   filters.push({
  //     name: filter.name,
  //     enabled: events.some((event) => filter.condition(event)), //,       
  //   });
  // });
  // return filters;
}

export {generateFilters};
